@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{text-align:center}.App .App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:30px;color:#fff}.App .App-header #quote-box{background-color:#fff;width:25%;border-radius:3px;padding:35px 100px;box-shadow:rgba(0,0,0,0.25) 0px 48px 100px 0px}@media only screen and (max-width: 1300px){.App .App-header #quote-box{padding:25px 75px}}@media only screen and (max-width: 1000px){.App .App-header #quote-box{width:45%;padding:25px 55px}}@media only screen and (max-width: 500px){.App .App-header #quote-box{width:55%;padding:25px 25px}}.App .App-header #quote-box #quote-icon{font-size:25px;opacity:0.5}.App .App-header #quote-box #text,.App .App-header #quote-box #author{letter-spacing:0.2px}.App .App-header #quote-box #text{font-size:28px;font-weight:400;line-height:1.3}@media only screen and (max-width: 1300px){.App .App-header #quote-box #text{font-size:24px}}@media only screen and (max-width: 500px){.App .App-header #quote-box #text{font-size:18px}}.App .App-header #quote-box #author{font-size:14px;font-weight:300;text-align:right}.App .App-header #quote-box #new-quote,.App .App-header #quote-box #tweet-quote{display:none}.App .App-header .links{display:flex;align-items:center;justify-content:space-between;margin-top:30px;width:calc(25% + 200px)}@media only screen and (max-width: 1300px){.App .App-header .links{width:calc(25% + 150px)}}@media only screen and (max-width: 1000px){.App .App-header .links{width:calc(45% + 110px)}}@media only screen and (max-width: 500px){.App .App-header .links{width:calc(55% + 50px)}}.App .App-header .links .new-quote,.App .App-header .links .tweet-quote{color:#fff;border-radius:3px;cursor:pointer;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.App .App-header .links .new-quote:hover,.App .App-header .links .tweet-quote:hover{-webkit-transform:scale(1.025);transform:scale(1.025)}.App .App-header .links .new-quote{order:2;font-family:"Poppins", sans-serif;font-size:10px;font-weight:600;text-transform:uppercase;letter-spacing:0.8px;border:none;padding:12px 15px}.App .App-header .links .tweet-quote{order:1;font-size:20px;padding:5px 10px}

